<template>
  <div class="container">
      <template v-if="authorizedUser">
          <template
            v-for="chat in chats"
            :key="chat.name"
          >
            <div v-if="chat.messages.length && chat.users.filter(user => user == authorizedUser).length" class="chats">
                <div class="chats__item">
                    <div class="chats__image"></div>
                    <div class="chats__text">
                        <div class="chats__username">{{chat.users.filter(user => user != authorizedUser)[0]}}</div>
                        <div class="chats__messageText">{{chat.messages[chat.messages.length-1].text}}</div>
                        <router-link :to="{path: '/'+chat.users.filter(user => user != authorizedUser)[0]+'/chat'}"></router-link>
                    </div>
                    <div class="chats__time">{{chat.messages[chat.messages.length-1].timeOfSend}}</div>
                </div>
            </div>
        </template>
      </template>
      <NotFound404 v-else></NotFound404>
    </div>
</template>

<script>
import {mapActions, mapState} from 'vuex'
// components
import NotFound404 from '../components/NotFound404';

 export default {
    name: "Chats",
    components:{
        NotFound404
    },
    computed: {
        ...mapState([
        'chats',
        'authorizedUser'
    ])
    },
    methods:{
        ...mapActions([
        'GET_CHATS',
        'CHECK_AUTORIZED'
    ])
    },
    mounted(){
        this.GET_CHATS()
        // autorized
        if(this.authorizedUser == ''){
        this.CHECK_AUTORIZED()
        }
    }
 }
</script>

<style lang="scss" scoped>
    .chats {
    padding-bottom: 20px;

    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;

        margin-top: 15px;
    }

        &__image {
            width: 50px;
            height: 50px;

            border-radius: 50%;

            background-color: rgb(177, 177, 177);
        }

        &__text {
            height: 100%;

            display: flex;
            flex-flow: column;
            justify-content: space-between;

            position: relative;

            a{
                position: absolute;

                width: 100%;
                height: 100%;
            }
        }

            &__username {
                color: black;
            }

            &__messageText {
                color: rgb(71, 71, 71);
            }

        &__time {
            color: rgb(71, 71, 71);
        }
}

</style>